body {
  font-family: 'Helvetica';
}
Main{
  border-style: inset ;
}
aside{
  border-style: inset ;
}
h1,
h2,
h3 {
  margin: 0.5rem 0;
}
h1 {
  color: #0b8cf5;
  padding:5px 5px;
  border: 1px solid darkblue;
  border-radius: 7px;
  font-size: 1.5rem;
}
h2 {
  font-size: 1.2rem;
}
h3 {
  font-size: 1rem;
}
a {
  padding: 0px 5px;
  text-decoration: none;
}
.row {
  display: flex;
  justify-content: space-between;
  
}
.row.center {
  align-items: center;
  
}
.col-1 {
  flex: 1;
}
.col-2 {
  flex: 2;
}
.block {
  background-color: #e0e0e0;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.text-right {
  text-align: right;
}
img.small {
  max-height: 8rem;
}
button {
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.5rem 0px;
  border-radius: 0.5rem;
  border: 0.1rem #404040 solid;
  background-color: #5fec43;
  cursor: pointer;
}
button.add {
  margin: 0.1rem;
  text-align: center;
  padding: 0px;
  font-weight:bolder ;
  background-color: #24db1e;
  width: 1.4rem;
  height: 1rem;
}
button.remove {
  text-align: center;
  margin: 0.1rem;
  padding: 0px;
  font-weight:bolder ;
  background-color: #ff0000;
  width: 1.4rem;
  height:1rem ;
}
button.badge {
  margin: 0.1rem;
  background-color: #136bb3;
  border: none;
  color: #ffffff;
  width: 1.5rem;
  height: 1.1rem;
 
}
